import React, { useEffect, useRef } from "react";
import '../styles/Contact.css';
import Grid from '@mui/material/Grid';
import illustration from '../assets/undraw_contact_us_re_4qqt.svg';

function Contact() {
    const formContainerRef = useRef(null); 
    
    useEffect(() => {
        // Délai pour simuler l'animation de chargement
        const timer = setTimeout(() => {
            if (formContainerRef.current) {
                formContainerRef.current.classList.add('loaded'); 
            }
        }, 50); 
        return () => clearTimeout(timer);
    }, []);

    return (
        <div className="footCont">
            <h2>Contactez-moi</h2>
            <div className="Conteneur-contact" id="contact">
                <Grid container spacing={4}>
                    <Grid item xs={12} md={4}>
                        <div className="contact-rapide">
                            <img src={illustration} alt="illustration-contact"/>
                        </div>
                    </Grid>
                    <Grid item xs={12} md={8} ref={formContainerRef}>
                        <div className="contact-container">
                            <form action="https://formspree.io/f/xkgwebod" method="POST" className="contact-form">
                                <div className="form-row">
                                    <div className="form-group">
                                        <label htmlFor="nom">Nom</label>
                                        <input type="text" id="nom" name="nom" required/>
                                    </div>
                                    <div className="form-group">
                                        <label htmlFor="prenom">Prénom</label>
                                        <input type="text" id="prenom" name="prenom" required/>
                                    </div>
                                </div>
                                <div className="form-group">
                                    <label htmlFor="email">Email</label>
                                    <input type="email" id="email" name="email" required/>
                                </div>
                                <div className="form-group">
                                    <label htmlFor="message">Message</label>
                                    <textarea id="message" name="message" rows="4" placeholder="Votre message" required></textarea>
                                </div>
                                <button type="submit" className="submit-btn">Envoyer</button>
                            </form>
                        </div>
                    </Grid>
                </Grid>
            </div>
        </div>
    );
}

export default Contact;
